import { useState, useEffect } from 'react';
import { supabase } from '@/lib/supabase';
import { logger } from '@/services/logger';

/**
 * Hook pour r√©cup√©rer le num√©ro de t√©l√©phone d'un utilisateur de mani√®re s√©curis√©e.
 * 
 * Le num√©ro de t√©l√©phone est prot√©g√© et n'est accessible que dans les cas l√©gitimes :
 * - L'utilisateur demande son propre t√©l√©phone
 * - Propri√©taire voit le t√©l√©phone de ses candidats
 * - Candidat voit le t√©l√©phone du propri√©taire qu'il a contact√©
 * - Parties d'un bail actif
 * - Administrateurs
 * 
 * @param userId - ID de l'utilisateur dont on veut r√©cup√©rer le t√©l√©phone
 * @returns { phone, loading } - Le num√©ro de t√©l√©phone (ou null) et l'√©tat de chargement
 */
export const useUserPhone = (userId: string | null) => {
  const [phone, setPhone] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (!userId) {
      setPhone(null);
      return;
    }

    const fetchPhone = async () => {
      setLoading(true);
      try {
        const { data, error } = await supabase.rpc('get_user_phone', {
          target_user_id: userId
        });
        
        if (error) {
          logger.error('Error fetching user phone', { error, userId });
          setPhone(null);
        } else {
          setPhone(data);
        }
      } catch (error) {
        logger.error('Exception fetching user phone', { error, userId });
        setPhone(null);
      } finally {
        setLoading(false);
      }
    };

    fetchPhone();
  }, [userId]);

  return { phone, loading };
};
